@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
      font-family: 'Montserrat', sans-serif;
  }
}

@layer components {
  button{
    @apply bg-pantone px-4 py-2 rounded hover:bg-pantone/80 duration-300 font-bold
  }

  .button {
    @apply bg-pantone px-4 py-2 rounded hover:bg-pantone/80 duration-300 font-bold
  }
  .slide-arrow{
    @apply p-1 bg-pantone cursor-pointer inline-flex items-center justify-center border border-transparent text-black rounded-lg hover:bg-transparent hover:text-pantone hover:border-pantone duration-300
  }

  .contact-list{
    @apply flex items-center bg-pantone/80 p-3 rounded-full text-black font-semibold mb-2
  }

  .selected-link{
    @apply text-pantone duration-500 bg-black p-1
  }

  .header-active {
    @apply relative border-b-2 border-pantone pb-2 duration-300 text-pantone
  }

